export const _calculateDateStringFromDate = (date: Date) => {
    if (date){
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
        const day = date.getDate();
        return `${year}-${month<10?`0${month}`:month}-${day<10?`0${day}`:day}`;
    }
    else {
        return ""
    }
}

export const _convertTimeStringToDate = (time: string, baseDate: Date) => {
    const newDate = new Date(baseDate);
    newDate.setHours(parseInt(time.split(":")[0]));
    newDate.setMinutes(parseInt(time.split(":")[1]));
    newDate.setSeconds(0, 0);
    return newDate;
}

export const _convertDateToTimeString = (date: Date) => {
    if (date){
        const hours = date.getHours();
        const minutes = date.getMinutes();
        return `${hours<10?`0${hours}`:hours}:${minutes<10?`0${minutes}`:minutes}`
    }
    else {
        return ""
    }
}

export const _convertDecimalToClock = (decimalTime:number) => {
    if (decimalTime === 24){
        return "24:00"
    }
    const n = new Date(0,0);
    n.setMinutes(+decimalTime * 60); 
    const result = n.toTimeString().slice(0, 5);
    return result
}
export const getLocale = () => {
    if (navigator.languages != undefined) 
    return navigator.languages[0]; 
    else 
    return navigator.language;
}