import { LitElement, property} from "lit-element";

export const PendingContainer = (Base: typeof LitElement) => {
    class PendingContainer extends Base {
        @property( {type: Boolean} ) __hasPendingChildren: boolean = false;
        @property( {type: Number} ) __pendingCount: number = 0;

        constructor(){
            super();
            this.addEventListener('pending-state', async (e: CustomEvent) => {
                this.__hasPendingChildren = true, 
                this.__pendingCount++;
                await e.detail.promise;
                this.__pendingCount--;
                this.__hasPendingChildren = this.__pendingCount !== 0;
            })
        }
    }
    return PendingContainer;
}