import { LitElement } from "lit-element";
import { PendingContainer } from "./pendingContainer";

export const Provider = (Base: typeof LitElement) => class Provider extends PendingContainer(Base){

    __instances = new Map();

    constructor(){
        super();

        this.addEventListener('request-instance', (event: CustomEvent) => {
            const key:string = event.detail.key;
            if (this.__instances.has(key)){
                event.detail.instance = this.__instances.get(key);
                event.stopPropagation();
            }
        })
    }

    provideInstance (key: string, instance:any){
        this.__instances.set(key, instance);
    }
}