import { LitElement} from "lit-element";
import { DayInterface } from "./interfaces/DayInterface";

interface RequesterEvent extends CustomEvent{
    detail: {
        key?:any,
        instance?:any
    }
}

export const Requester = (Base: typeof LitElement) => class Requester extends Base {
    
    public requestInstance(key:any) {
        
        const event: RequesterEvent = new CustomEvent('request-instance', {
            detail: {key},
            bubbles: true, 
            composed: true, 
            cancelable: true
        });

        this.dispatchEvent(event);

        return event.detail.instance;
    }

    public setTime(startTime: Date, endTime: Date){
        const event = new CustomEvent('time-change', {
            detail: {
                startTime: startTime,
                endTime: endTime
            },
            bubbles: true, 
            composed: true, 
        });
        this.dispatchEvent(event);
    };   
    
    public addBreak(breakString: string){
        const event = new CustomEvent('add-break', {
            detail: {
                breakString: breakString
            },
            bubbles: true, 
            composed: true, 
        });
        this.dispatchEvent(event);
    };    
    public deleteBreak(index: number){
        const event = new CustomEvent('delete-break', {
            detail: {
                index: index
            },
            bubbles: true, 
            composed: true, 
        });
        this.dispatchEvent(event);
    }; 

    public saveDay(){
        const event = new CustomEvent('save-day', {
            bubbles: true, 
            composed: true, 
        });
        this.dispatchEvent(event);
    }

    public loadDay(dateString: string){
        const event = new CustomEvent('load-day', {
            detail: {
                dateString: dateString
            },
            bubbles: true, 
            composed: true, 
        });
        this.dispatchEvent(event);
    }

    public deleteDay(){
        const event = new CustomEvent('delete-day', {
            bubbles: true, 
            composed: true, 
        });
        this.dispatchEvent(event);
    }
};