import { LitElement, html, customElement, property, css } from "lit-element";
import { openDB, IDBPDatabase } from 'idb';

//components
import "@material/mwc-icon-button";
import "@material/mwc-button";
import "@material/mwc-drawer";
import "@material/mwc-top-app-bar"
import 'app-datepicker';

//views
import './views/day-view'
import './views/view-template'
import './views/not-found-view'

//helpers
import Router from "./router"

// Interfaces
import { ViewInterface, View } from "./interfaces/ViewInterface";
import { SavedDataInterface } from "./interfaces/SavedData";
import { StateInterface } from "./interfaces/StateInterface";
import { DayInterface } from "./interfaces/DayInterface";
import { Provider } from "./provider";
import { DefaultTimesInterface } from "./interfaces/DefaultTimesInterface";
import { DaysDBSchema } from "./interfaces/DaysDBSchema";
import { _calculateDateStringFromDate, getLocale } from "./helpers";

//https://github.com/web-padawan/awesome-lit-html
//https://github.com/motss/app-datepicker/blob/master/api-references.md#appdatepicker
//https://motss-app.web.app/demo/app-datepicker.html
//https://github.com/BrightspaceUI/core/blob/master/components/inputs/docs/input-date-time.md
//https://github.com/fsx950223/chartjs-web-components
//https://materializecss.com/pickers.html

const STORAGE_KEY = '__zeitkontrast__'
const author = 'PixelQuasar';
const homepage = 'https://open-wc.org/';
const footerTemplate = html`
  <footer>Made with love by <a href="${homepage}">${author}</a></footer>
`;

@customElement('zeitkontrast-app')
class ZeitkontrastApp extends Provider(LitElement) {
    @property ({ type: Boolean}) drawerOpen: boolean = false;
    @property ({ type: String}) currentView: View = "dayView";
    @property ({ attribute: false}) router: Router;
    @property ({ type: Object}) state: StateInterface;
    @property ({ attribute: false}) db: IDBPDatabase<DaysDBSchema>

    constructor() {
        super();
        this.router = new Router();
        this._installRoutes();
        this._installListeners();
        this._initiateState();
    }

    firstUpdated(){

    }

    private _initiateState(){
        const state: StateInterface = {
            currentDayData: undefined,
            defaultTimes: {startHours: undefined, endHours: undefined, startMinutes: undefined, endMinutes: undefined},
            saveDisabled: false,
            locale: getLocale()
        }
        this.state = {... this.state, ... state};
    }

    private async _openDB(){
        const db = await openDB<DaysDBSchema>('Days', 1, {
            upgrade(db) {
              // Create a store of objects
              const store = db.createObjectStore('days');
              // Create an index on the 'date' property of the objects.
              //store.createIndex('startTime', 'date');
              //store.createIndex('endTime', 'date');
            },
          });
        if (db){
            this.db = db;
            console.log("dbloaded")
        }
        else {
            console.log("error loading db");
        }
    }

    private _installListeners(){

        this.addEventListener('time-change', (e:CustomEvent) => {
            const startTime: Date = e.detail.startTime;
            const endTime: Date = e.detail.endTime;
            this.state = {
                ... this.state, 
                currentDayData: {... this.state.currentDayData, startTime: startTime, endTime: endTime},
                saveDisabled: false
            }
        })
        this.addEventListener('add-break', (e:CustomEvent) => {
            const breakString: string = e.detail.breakString;
            this.state = {
                ... this.state, 
                currentDayData: {... this.state.currentDayData, breaks: [... this.state.currentDayData.breaks, breakString]},
                saveDisabled: false
            }
        })
        this.addEventListener('delete-break', (e:CustomEvent) => {
            const _index: number = e.detail.index;
            this.state = {
                ... this.state, 
                currentDayData: {... this.state.currentDayData, breaks: this.state.currentDayData.breaks.filter((_break, index) => index !== _index)},
                saveDisabled: false
            }
        })
        this.addEventListener('save-day', (e:CustomEvent) => {
            this._saveDay();
        })
        this.addEventListener('delete-day', (e:CustomEvent) => {
            this._deleteDay();
        })
        this.addEventListener('load-day', (e:CustomEvent) => {
            const dateString: string = e.detail.dateString;
            this._loadDay(dateString);
        })
    }

    connectedCallback() {
        super.connectedCallback();
        console.log('app connected');
    }

    updated(changedProperties:Map<string, any>){
        /* if (changedProperties.has("state")){
        } */
    }

    static get styles() {
        return css`
                header{
                    display:flex;
                    flex-direction: column;
                    align-items: center;
                    font-family: monospace;
                }
                mwc-top-app-bar{
                    --mdc-theme-primary: transparent;
                    --mdc-theme-on-primary: black;
                }
            `
    }

    render() {
        return html`
            <mwc-drawer hasHeader type="modal" .open=${this.drawerOpen} @MDCDrawer:closed=${() => this.drawerOpen=false}>
                <span slot="title">Drawer Title</span>
                <span slot="subtitle">subtitle</span>
                <div>
                    ${this._renderDrawerContent()}
                </div>
                <div slot="appContent">
                        <mwc-top-app-bar centerTitle>
                            <mwc-icon-button @click=${() => this._toggleDrawer(this.drawerOpen)} slot="navigationIcon" icon="menu"></mwc-icon-button>
                            <div slot="title">time contrast</div>
                        </mwc-top-app-bar>
                        ${this._renderCurrentView(this.currentView)}
                        <!-- ${footerTemplate} -->
                </div>
            </mwc-drawer>
        `;
    }

    private _renderDrawerContent(){
        return html`
            <li @click=${() => this._changeView({view:"dayView"})} >Day View</li>
            <li @click=${() => this._changeView({view:"viewTemplate"})}>View Template</li>
            <li @click=${() => this._changeView({view:"notFoundView"})}>Not Found Test</li>
        `
    }

    private _renderCurrentView(currentView: View){
        switch (currentView) {
            case "dayView":
                return html`
                    <day-view
                        .state=${this.state}
                    ></day-view>
                `
            case "viewTemplate":
                return html`
                    <view-template></view-template>
                `
            case "notFoundView":
                return html`
                    <not-found-view></not-found-view>
                `
            default:
                return html`
                    <not-found-view></not-found-view>
                `
        }
    }

    private _installRoutes(){

        const view: View = this.router.getParameter("view");
        if (!view){
            this.router.setParameter("view", "dayView");
            this.router.applyParameters;
        }

        this._applyRouteByParameters();

        this.addEventListener('change-view', (e:CustomEvent) => {
            const viewData: ViewInterface = e.detail;
            const view: View = viewData.view;
            this.router.clearParameters();

            const navigate = (view: View) => {
                this.router.setParameter("view", view);
                this.router.applyParameters();
                this._applyRouteByParameters();
            }

            switch(view){
                case "dayView":
                    navigate(view);
                    break;
                case "viewTemplate":
                    navigate(view);
                    break;
                case "notFoundView": 
                    navigate(view);
                    break;
            }
        })
    }

    private _applyRouteByParameters(){
        const view: View = this.router.getParameter("view");
        switch (view) {
            case "dayView": 
                this._dayViewRoute();
                break;
            case "viewTemplate": 
                this._viewTemplateRoute();
                break;
            case "notFoundView": 
                this._notFoundRoute();
                break;
            default:
                this._notFoundRoute();
                break;
        }
    }

    private _changeView(viewData: ViewInterface){
        const event = new CustomEvent('change-view', {
            bubbles: true, 
            composed: true,
            detail: viewData
        })
        this.dispatchEvent(event);
    }

    private _dayViewRoute = () => {
        this.currentView = "dayView";
    }

    private _viewTemplateRoute = () => {
        this.currentView = "viewTemplate";
    }

    private _notFoundRoute = () => {
        this.currentView = "notFoundView";
    }

    private _toggleDrawer(drawerOpen:boolean){
        drawerOpen?this.drawerOpen=false:this.drawerOpen=true;
    }
    
    private _setDefaultTimes(days: DayInterface[]) {
        const now = new Date();
        let defaultTimes: DefaultTimesInterface;
        if (days?.length>0){
            const lastSavedDay = new Date(days[days.length - 1].startTime);
            defaultTimes = {
                startHours: lastSavedDay.getHours(), 
                startMinutes: lastSavedDay.getMinutes(),
                endHours: now.getHours(),
                endMinutes: now.getMinutes()
            }
        }
        else {
            defaultTimes = {
                startHours: 8,
                startMinutes: 0,
                endHours: now.getHours(),
                endMinutes: now.getMinutes()
            }
        }
        this.state = {... this.state, defaultTimes: defaultTimes};
    }

    private async _saveDay(){
        if (!this.db){
            await this._openDB();
        }
        const day = {... this.state.currentDayData, exists: true};
        const key = _calculateDateStringFromDate(day.startTime);
        const save = await this.db.put('days', {
            ... day
          }, key);
        console.log("dbsave", save);
        const now = new Date();
        let defaultTimes = {
            startHours: day.startTime.getHours(), 
            startMinutes: day.startTime.getMinutes(),
            endHours: now.getHours(),
            endMinutes: now.getMinutes()
        }
        this.state = {... this.state, currentDayData: day, defaultTimes: defaultTimes, saveDisabled: true};
    }

    private async _loadDay(dateString: string){
        console.log(dateString);
        try {
            if (!this.db){
                await this._openDB();
            }
            let day = await this.db.get("days", dateString);
            console.log("dbload", day);
            if (day){
                this.state = {...this.state, currentDayData: day, saveDisabled: true};
            }
            else {
                const now = new Date();
                const defaultTimes = this.state?.defaultTimes;
                if (!defaultTimes?.startHours){
                    defaultTimes.startHours = 8;
                }
                if (!defaultTimes?.startMinutes){
                    defaultTimes.startMinutes = 30;
                }
                if (!defaultTimes?.endHours){
                    defaultTimes.endHours = now.getHours();
                }
                if (!defaultTimes.endMinutes){
                    defaultTimes.endMinutes = now.getMinutes();
                }
                this.state = {... this.state, defaultTimes: defaultTimes};
    
                const startTime = new Date(dateString);
                startTime.setHours(this.state.defaultTimes.startHours);
                startTime.setMinutes(this.state.defaultTimes.startMinutes);
                const endTime = new Date(dateString);
                endTime.setHours(this.state.defaultTimes.endHours);
                endTime.setMinutes(this.state.defaultTimes.endMinutes);
                if (endTime < startTime){
                    endTime.setDate(endTime.getDate()+1);
                }
                let newday: DayInterface = {
                    startTime: startTime,
                    endTime: endTime,
                    breaks: [],
                    exists: false
                }
                this.state = {...this.state, currentDayData: newday, saveDisabled: false}
            }
        }
        catch (err) {
            console.log('failed loading day', err);
        }

        
    }

    private async _deleteDay(){
        const day = this.state.currentDayData;
        const key = _calculateDateStringFromDate(day.startTime);
        if (!this.db){
            await this._openDB();
        }
        let deleteResponse = await this.db.delete("days", key);
        console.log("dbload", deleteResponse);

        this.state = {... this.state, currentDayData: undefined}
    }
}
